@import url(https://fonts.googleapis.com/css?family=Google+Sans:100,300,400,500,700,900,100i,300i,400i,500i,700i,900i);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Google Sans', 'Roboto', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #555;
}

* {
  font-family: inherit !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
}

.text-muted {
  color: #888;
}

.m-1 {
  margin: .5rem !important;
}

.m-r-1 {
  margin-right: .5rem !important;
}

.clipped {
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-link {
  text-decoration: none;
  font-size: 1rem;
}

i.fa {
  font-family: FontAwesome !important;
}

a {
  text-decoration:none;
}

a:visited {
  text-decoration:none;
}

a[href^="#"] {
  color: inherit;
}

.hero {
    padding: 3rem 0 3rem 0;
    background-color: #f5f5f5;
}

img.logo {
    width: 100%;
    max-width: 15rem;
}

.about-us-image {
    height: 22rem;
    max-width: 100%;
    background-image: url(/gdg-astra/static/media/about-us.6b0727b8.jpg);
    background-position-y: 100%;
}

.thumb-image {
    display: inline-block;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 4rem;
    margin-right: -1rem;
    background-size: cover;
    border: .1rem solid #fff;
}

.numbers {
    margin-bottom: 1rem;
}

.numbers .title span {
    font-size: 1.2rem;
    display: block;
}

.numbers .value {
    font-size: 3rem;
    text-align: center;
}

h2 {
    margin: 1em 0 !important;
}

#about-us {
    background-color: #fff;
    padding: 0 !important;
    background-image: url(/gdg-astra/static/media/about-us.6b0727b8.jpg);
    background-size: cover;
    background-position-y: 50%;
    color: #fff;
}

#about-us a {
    color: #fff;
}

#about-us > div {
    padding: 2rem 1rem 3rem 1rem;
    background-color: rgba(0,0,0,0.6);
}

.feature {
    text-align: center;
    font-size: 1rem; 
}

.feature svg {
    color: #fff;
    width: 2rem;
    height: auto;
}

.feature .subtitle {
    margin-top: .2rem;
    font-size: .8rem;
}

p svg {
    position:relative;
    bottom: -4px;
}

#team {
    background-color: #fff;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.team-member {
    padding-bottom: 1rem;
}

.team-member .photo {
    display: inline;
    margin-right: 1rem;
}

.team-member .photo img {
    width: 100px;
    border-radius: 200px;
    margin-left: 10px;
}

.team-member .photo {
    float: left;
}

#contacts {
    padding: 0 !important;
}

#contacts > div {
    padding: 1rem;
}

.footer {
    padding: 3rem 1rem 1rem 1rem;
    text-align: center;
    color: #888;
}

.footer .media {
    font-size: 1.2rem;
    margin: .5rem;
    color: #888;
}
